import { clearFix } from 'polished'
import { Props } from 'react'
import styled from 'styled-components'

export const Row = styled.div<RowProps>`
  ${clearFix()}
  margin: ${props => props.margin ? props.margin : `20px 0`};
  ${props => props.justify && `
    display: flex;
    justify-content: ${props.justify};
  `}
`

export const Col = styled.div<ColProps>`
  float: left;
  min-height: 1px;
  width: ${props => props.span * 100 / 12}%;
  padding: 0 ${props => props.gutter === undefined ? 15 : props.gutter}px;

/* sm screen like iphone */
@media all and (width < 768px){
  ${props => props.sm && `
    width: ${props.sm * 100 / 12}%;
  `}
}

/* xs screen */
  @media all and (width < 576px){
    ${props => props.xs && `
      width: ${props.xs * 100 / 12}%;
    `}
  }

/* md screen like ipad */
   @media all and (768px <= width < 1200px){
    ${props => props.md && `
      width: ${props.md * 100 / 12}%;
    `}
  }

/* lg screen for most desktop */
   @media all and (1200px <= width <= 1920px){
    ${props => props.lg && `
      width: ${props.lg * 100 / 12}%;
    `}
  }

  /* xl sreen like customized display */
   @media all and (width > 1920px){
    ${props => props.xl && `
      width: ${props.xl * 100 / 12}%;
    `}
  }
`

// 12
interface ColProps extends Props<HTMLDivElement> {
  gutter?: number
  span: number // (mdlg)
  xs?: number // 
  sm?: number // 
  md?: number // 
  lg?: number // 
  xl?: number // 
}

interface RowProps {
  justify?: 'end' | 'center' | 'space-between' | 'space-around'
  margin?: string
}